import React from 'react'
import CubicBezier from 'cubic-bezier-object'
import AIMessage from './ai-message'
import AIThinkMessage from './ai-think-message'
import ClientMessage from './client-message'

const MSG_TYPES = {
	THINK: 'q-t',
	ANSWER: 'a',
	QUESTION: 'q'
}
/**
 * 
 */
class Chat extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			dialogues: [],
			lastThinkingIndex: -1
		};
		this.optionsRoll = {};

		console.debug('q:' + JSON.stringify({
			type: MSG_TYPES.QUESTION,
			msg: '?',
			result: true,
			options: [
				{ value: 'F', label: '' },
				{ value: 'M', label: '' }
			]
		}))
	}
	componentDidMount() {
		this.getDialogues().push({
			type: MSG_TYPES.QUESTION,
			msg: ', . , , .',
			timestamp: new Date()
		});
		this.getDialogues().push({
			type: MSG_TYPES.QUESTION,
			msg: '.',
			timestamp: new Date()
		});
		this.pushAIThinking();
		setTimeout(() => {
			this.pushQuestion({
				type: MSG_TYPES.QUESTION,
				msg: '?',
				timestamp: new Date(),
				options: [
					{ value: 1, label: '0 - 12' },
					{ value: 2, label: '13 - 18' },
					{ value: 3, label: '19 - 25' },
					{ value: 4, label: '26 - 40' },
					{ value: 5, label: '41 - 60' },
					{ value: 6, label: '61' }
				]
			});
		}, 1000);
		// this.getDialogues().push({
		// 	type: MSG_TYPES.ANSWER,
		// 	msg: 'Yes askldjfhlaksdhgklashjdfglksdjhfglku ewytroihaglkjsbdgkljbasjhbjasdfuytvabigrtkwqjbtrq',
		// 	timestamp: new Date()
		// });
		// this.getDialogues().push({
		// 	type: MSG_TYPES.ANSWER,
		// 	msg: 'Yes askldjfhlaksdhgklashjdfglksdjhfglku ewytroihaglkjsbdgkljbasjhbjasdfuytvabigrtkwqjbtrq',
		// 	timestamp: new Date()
		// });
		// this.getDialogues().push({
		// 	type: MSG_TYPES.ANSWER,
		// 	msg: 'Yes askldjfhlaksdhgklashjdfglksdjhfglku ewytroihaglkjsbdgkljbasjhbjasdfuytvabigrtkwqjbtrq',
		// 	timestamp: new Date()
		// });
		// this.pushAIThinking();
	}
	renderDialogues() {
		return this.getDialogues().map((dialogue, index) => {
			return React.createElement(this.matchMessageType(dialogue), {
				key: index,
				dialogue: dialogue
			});
		});
	}
	renderLastOptions() {
		const lastQuestion = this.getDialogues().slice(0).reverse().find(dialogue => {
			return dialogue.type === MSG_TYPES.QUESTION || dialogue.type === MSG_TYPES.THINK;
		});
		if (lastQuestion == null
			|| lastQuestion.type !== MSG_TYPES.QUESTION
			|| lastQuestion.options == null
			|| lastQuestion.options.length === 0) {
			return;
		}

		return <div className='bunny-chat-options'
			ref='options'>
			<div onTouchStart={this.onOptionsTouchStart}
				onTouchMove={this.onOptionsTouchMove}
				onTouchEnd={this.onOptionsTouchEnd}
				ref='optionsRoll'>
				{lastQuestion.options.map((option, index) => {
					return <span
						onClick={this.onOptionClicked.bind(this, option)}
						key={index}>
						{option.label}
					</span>
				})}
			</div>
		</div>
	}
	renderRestart() {
		return <div className='bunny-chat-restart' ref='restart'>
			<div className='bunny-restart-backdrop' />
			<div className='bunny-restart-alert'>
				<div className='bunny-restart-message'>
					<span>?</span>
				</div>
				<div className='bunny-restart-buttons'>
					<span onClick={this.onRestartCancelClicked}>
						
					</span>
					<span onClick={this.onRestartConfirmClicked}>
						
					</span>
				</div>
			</div>
		</div>
	}
	render() {
		return <div className='bunny-chat' style={{
			height: `calc(100vh - ${this.getHeaderHeight()}px)`
		}}>
			<div className='bunny-chat-content' ref='content'>
				{this.renderDialogues()}
			</div>
			{this.renderLastOptions()}
			<div className={`bunny-chat-button-bar ${this.hasResult() ? 'bunny-chat-has-result' : ''}`}>
				<i className='bg-restart bunny-chat-button'
					onClick={this.onRestartClicked} />
				<input type='text' className='bunny-chat-text'
					ref='text' />
				<i className='bg-send bunny-chat-button'
					onClick={this.onSendClicked} />
				<i className='bg-magic bunny-chat-button'
					onClick={this.onResultClicked} />
			</div>
			{this.renderRestart()}
		</div>
	}
	getHeaderHeight() {
		return this.props.headerHeight;
	}
	getDialogues() {
		return this.state.dialogues;
	}
	hasResult() {
		const dialogues = this.getDialogues();
		return dialogues.find(dialogue => {
			return dialogue.result;
		});
	}
	matchMessageType(dialogue) {
		switch (dialogue.type) {
			case 'a':
				return ClientMessage;
			default:
				return this.matchAIMessage(dialogue);
		}
	}
	matchAIMessage(dialogue) {
		switch (dialogue.type) {
			case MSG_TYPES.THINK:
				return AIThinkMessage;
			default:
				return AIMessage
		}
	}
	pushAnswer(text) {
		if (text.startsWith('q:')) {
			// , 
			this.refs.text.value = '';
			this.pushQuestion(text);
			return;
		}

		const dialogues = this.getDialogues();
		dialogues.push({
			type: 'a',
			msg: text,
			timestamp: new Date()
		});
		this.refs.text.value = '';
		if (this.state.lastThinkingIndex !== -1) {
			// 
			// 
			dialogues.splice(this.state.lastThinkingIndex, 1);
			this.pushAIThinking();
		} else {
			// , 
			this.forceUpdate(() => {
				this.autoScrollToEnd();
				// , 
				setTimeout(() => {
					this.pushAIThinking();
				}, 300);
			});
		}
	}
	pushQuestion(question) {
		let q = question;
		if (typeof question === 'string') {
			q = JSON.parse(question.substring(2));
			q.timestamp = new Date();
		}
		const dialogues = this.getDialogues();
		if (this.state.lastThinkingIndex !== -1) {
			// , 
			dialogues.splice(this.state.lastThinkingIndex, 1);
		}
		dialogues.push(q);
		this.setState({
			lastThinkingIndex: -1
		}, () => {
			this.autoScrollToEnd();
		});
	}
	pushAIThinking() {
		const dialogues = this.getDialogues();
		dialogues.push({
			type: MSG_TYPES.THINK
		});
		this.setState({
			lastThinkingIndex: dialogues.length - 1
		}, () => {
			this.autoScrollToEnd();
		});
	}
	restartSession() {
		const dialogues = this.getDialogues();
		dialogues.length = 0;
		dialogues.push({
			type: MSG_TYPES.QUESTION,
			msg: ', .',
			timestamp: new Date()
		});
		this.setState({
			lastThinkingIndex: -1
		}, () => {
			this.autoScrollToEnd();
		});
	}
	autoScrollToEnd() {
		const content = this.refs.content;
		const gap = content.scrollHeight - content.scrollTop - content.clientHeight;
		content.scrollTop += gap;
	}
	hideRestartAlert(restart) {
		this.refs.restart.classList.add('bunny-restart-hiding');
		setTimeout(() => {
			this.refs.restart.classList.remove('bunny-restart-hiding', 'bunny-restart-show');
			if (restart) {
				this.restartSession();
			}
		}, 400);
	}
	onRestartClicked = () => {
		this.refs.restart.classList.add('bunny-restart-show');
	}
	onRestartCancelClicked = () => {
		this.hideRestartAlert(false);
	}
	onRestartConfirmClicked = () => {
		this.hideRestartAlert(true);
	}
	onSendClicked = () => {
		const text = this.refs.text.value;
		if (text.length === 0) {
			return;
		}

		this.pushAnswer(text);
	}
	onResultClicked = () => {
		this.props.app.showResults();
	}
	onOptionsTouchStart = (evt) => {
		let currentOffset = this.refs.optionsRoll.style.transform;
		if (currentOffset) {
			currentOffset = currentOffset.replace('translateX(', '').replace('px)', '');
		}
		this.optionsRoll = {
			startX: evt.touches[0].pageX,
			tracks: [{
				x: evt.touches[0].pageX,
				time: Date.now(),
			}],
			direction: 'left',
			currentOffset: currentOffset * 1
		}
	}
	onOptionsTouchMove = (evt) => {
		const offsetX = evt.touches[0].pageX - this.optionsRoll.startX;
		// , Offset0; 
		const originalDirection = this.optionsRoll.direction;
		this.optionsRoll.direction = offsetX >= 0 ? 'right' : 'left';
		if (originalDirection !== this.optionsRoll.direction) {
			// , , 
			// 
			this.optionsRoll.tracks = this.optionsRoll.tracks.slice(this.optionsRoll.tracks.length - 1);
		}
		// 
		this.optionsRoll.tracks.push({
			x: evt.touches[0].pageX,
			time: Date.now()
		});
		this.optionsRoll.startX = evt.touches[0].pageX;
		this.optionsRoll.currentOffset += offsetX;
		// 0, 
		this.optionsRoll.currentOffset = Math.min(0, this.optionsRoll.currentOffset);
		const width = this.refs.optionsRoll.clientWidth;
		const containerWith = this.refs.options.clientWidth;
		// , 
		this.optionsRoll.currentOffset = Math.max(this.optionsRoll.currentOffset, containerWith - width);
		this.refs.optionsRoll.style.transform = `translateX(${this.optionsRoll.currentOffset}px)`;
	}
	onOptionsTouchEnd = (evt) => {
		const now = Date.now();
		const lastMoveTime = this.optionsRoll.tracks[this.optionsRoll.tracks.length - 1].time;
		if (now - lastMoveTime >= 100) {
			// 100, 
			return;
		}
		else {
			// 100
			const tracks = this.optionsRoll.tracks.reverse();
			const lastMoveTime = tracks[0].time;
			// 100
			const index = tracks.findIndex(track => {
				return track.time - lastMoveTime < -100;
			});
			let lastUsefulTrack = null;
			if (index === -1) {
				// 100
				lastUsefulTrack = tracks[tracks.length - 1];
			}
			else if (tracks.length === 0) {
				// , 
				lastUsefulTrack = tracks[0];
			}
			else {
				// 100, ()
				lastUsefulTrack = tracks[index - 1];
			}
			const offset = Math.abs(lastUsefulTrack.x - tracks[0].x);
			// console.log('offset', offset);
			if (offset <= 20) {
				// 20, 
				return;
			} else {
				// , , 
				let speed = offset / (lastMoveTime - lastUsefulTrack.time);
				// 110
				// 
				// 
				speed = Math.min(1, speed);
				// 
				const availableInertiaOffset = speed * 10 * 50;
				// console.log('availableInertiaOffset', availableInertiaOffset);
				// 
				let finalOffset = null;
				if (this.optionsRoll.direction === 'left') {
					// 
					finalOffset = Math.ceil(this.optionsRoll.currentOffset - availableInertiaOffset);
				} else if (this.optionsRoll.direction === 'right') {
					// 
					finalOffset = Math.ceil(this.optionsRoll.currentOffset + availableInertiaOffset);
				}
				// 0, 
				finalOffset = Math.min(0, finalOffset);
				const width = this.refs.optionsRoll.clientWidth;
				const containerWith = this.refs.options.clientWidth;
				// , 
				finalOffset = Math.max(finalOffset, containerWith - width);
				this.refs.optionsRoll.style.transform = `translateX(${this.optionsRoll.currentOffset}px)`;
				// , 20, 
				const inertiaOffset = finalOffset - this.optionsRoll.currentOffset;
				// console.log('inertiaOffset', inertiaOffset);
				let index = 0;
				const steps = 20;
				const cubic = new CubicBezier('cubic-bezier(.5, 1.49, .75, .97)');
				let inertia = () => {
					const offset = this.optionsRoll.currentOffset + inertiaOffset * cubic.getY((index + 1) / steps);
					this.refs.optionsRoll.style.transform = `translateX(${offset}px)`;
					// const promise = this.doRoll(rolling, );
					index++;
					if (index < steps) {
						setTimeout(inertia, 50);
					}
				};
				inertia();
			}
		}
	}
	onOptionClicked(option) {
		this.pushAnswer(option.label);
	}
}

export default Chat


